<template>
    <v-card>
        <v-card-title>
            Cards
            <v-spacer></v-spacer>
            <v-text-field
                v-model="search"
                label="Search by a name or a keyword..."
                single-line
                hide-details
            ></v-text-field>
        </v-card-title>
        <v-data-table
            :headers="headers"
            :items="cards"
            :sort-by="['games', 'winrate']"
            :sort-desc="[true, true]"
            :search="search"
            multi-sort
            class="elevation-1"
            style="height:100%"
            :loading="isLoading"
        >
            <template v-slot:item.cardCode="{ item }">
                <v-avatar class="ma-1">
                    <v-img :src="'./assets/'+item.cardCode+'-full.jpg'" />
                </v-avatar>
            </template>
            <template v-slot:item.detail="{ item }">
                <v-btn :to="`/card?code=${item.cardCode}`">
                    Show Details
                </v-btn>
            </template>
            <template v-slot:item.winrate="{ item }">
                {{item.winrate}} %
            </template>
        </v-data-table>
    </v-card>  
</template>

<script>
/*eslint no-console: ["error", { allow: ["log"] }] */
  import * as get from "../../utils/get.js";

  export default {
    props: {
        filter: { type: String }
    },
    data () {
      return {
        headers: [
          { text: '', value: 'cardCode', sortable: false },
          { text: 'Card Name', value: 'name', sortable: true },
          { text: 'Winrate', value: 'winrate', sortable: true },
          { text: 'Games', value: 'games', sortable: true },
          { text: 'Copies used', value: 'copies', sortable: true },
          { text: 'Rarity', value: 'rarity', sortable: false },
          { text: 'Region', value: 'region', sortable: true },
          { text: 'Description', value: 'descriptionRaw', sortable: false },
          { text: '', value: 'detail', sortable: false },
        ],
        cards: [],
        isLoading: false,
        search: ""
      }
    },
    mounted () {
      this.getListCards(`[{"field":"collectible", "value":true}]`);
    },
    methods: {
      async getListCards (filter = "") {
        this.isLoading = true;
        let listCards = await get.getCards(500, filter, true);
        listCards.data.forEach((card) => {
          card.games = card.wins+card.losses;
          card.games ? card.winrate = Math.round(card.wins/(card.games)*10000)/100 : card.winrate = 0;
        });
        console.log(listCards.data);
        this.cards = listCards.data;
        this.isLoading = false
      }
    },
    watch: {
      filter: {
        handler: function(val){
          console.log(val);
          this.getListCards(val)
        },
        deep: true
      },
    }
  }
</script>