import Vue from 'vue'
import App from './App.vue'
import Home from './components/home/Home.vue'
import Deck from './components/home/Deck.vue'
import Cards from './components/cards/Cards.vue'
import Card from './components/cards/Card.vue'
import Replays from './components/replays/Replays.vue'
import Replay from './components/replays/Replay.vue'
import vuetify from './plugins/vuetify';
import "vuetify/dist/vuetify.min.css";
import VueRouter from 'vue-router';

Vue.config.productionTip = false

Vue.use(VueRouter);

const routes = [
  { path: '/', component: Home },
  { path: '/cards', component: Cards },
  { path: '/card', component: Card },
  { path: '/deck', component: Deck },
  { path: '/replays', component: Replays },
  { path: '/replay', component: Replay },
];

const router = new VueRouter({
    routes
});

new Vue({
  router,
  vuetify,
  render: h => h(App)
}).$mount('#app')
