<template>
    <v-row class="cards-row">
        <span 
            v-for="(card, index) in cards" 
            v-bind:key="index"
            :style="'left:'+(index*part)+'px;z-index:'+(40-index)"
        > 
            <v-tooltip left>
                <template v-slot:activator="{ on }">
                    <v-img 
                        v-bind:src="'./assets/'+card.cardCode+'-full.jpg'"
                        class="card-thumb"
                        v-on="on"
                    />
                </template>
                <span>
                    <v-img 
                        v-bind:src="'./assets/'+card.cardCode+'.png'"
                        class="card-tooltip"
                        v-on="on"
                    />
                </span>
            </v-tooltip>
            <v-card-text class="card-count">x{{card.count}}</v-card-text>
        </span>
    </v-row>
</template>

<script>
/*eslint no-console: ["error", { allow: ["log"] }] */
export default {
    props: {
        cards: { type: Array }
    },
    data() {
        return {
            part: (700/this.cards.length)
        }
    }
};
</script>
<style>
    .card-thumb {
        width: 40px;
        height: 40px;
        background-size: cover !important;
        border-radius: 100%;
        flex: none !important;
        border:2px solid #212121;
    }
    .card-count {
        border-radius: 16px;
        font-size: 14px;
        height: 12px;
        position: absolute;
        min-width: 10px;
        padding: 0px;
        width: 12px;
        margin-top: -34px;
        margin-left: 7px;
        z-index: 41;
        color: #F5F5F5;
        font-weight: bold !important;
        -webkit-text-stroke: 2px #212121;
        paint-order: stroke fill;
        pointer-events: none;
    }
    .cards-row {
        width: 600px;
        position: relative;
    }
    .cards-row > span {
        position: absolute;
        margin-top: -21px
    }
    .v-tooltip__content {
        z-index: 9999 !important;
    }
    .card-tooltip {
        width: 250px;
    }
    .v-tooltip__content {
        background: none !important;
        margin-left:30px;
    }
</style>