<template>
  <v-data-table
    :headers="headers"
    :items="decks"
    :sort-by="['winrate', 'games']"
    :sort-desc="[true, true]"
    multi-sort
    class="elevation-1"
    style="height:100%;position:relative;z-index:1"
    :loading="isLoading"
  >
    <template v-slot:item.cardsInDeck="{ item }">
        <Cards v-bind:cards="item.cardsInDeck"/>
    </template>
    <template v-slot:item.winrate="{ item }">
        {{item.winrate}} %
    </template>
    <template v-slot:item.detail="{ item }">
        <v-btn :to="`/deck?code=${item.deckCode}`">
            Show Details
        </v-btn>
    </template>
  </v-data-table>
</template>

<script>
/*eslint no-console: ["error", { allow: ["log"] }] */
  import Cards from "./Cards.vue";
  import * as get from "../../utils/get.js";

  export default {
    components: {
        Cards
    },
    props: {
        filterProp: { type: String },
        timeFrameProp: { type: Number },
        minGamesProp: { type: Number }
    },
    data () {
      return {
        headers: [
          { text: 'Archetype', value: 'archetype', sortable: false },
          { text: 'Winrate', value: 'winrate' },
          { text: 'Games', value: 'games' },
          { text: 'Cards', value: 'cardsInDeck', sortable: false },
          { text: '', value: 'detail', sortable: false }
        ],
        decks: [],
        isLoading: false,
        filter: "",
        timeFrame: 30,
        minGames: 1
      }
    },
    mounted () {
      this.getListDecks();
    },
    methods: {
      async getListDecks (filter = "", timeFrame = 30) {
        this.isLoading = true;
        let listDecks = await get.getDecks(filter, timeFrame);
        listDecks.data.forEach((deck) => {
          deck.archetype = deck.factions.join(" / ")
          deck.games = deck.wins+deck.losses;
          deck.games ? deck.winrate = Math.round(deck.wins/(deck.games)*10000)/100 : deck.winrate = 0;
        });
        this.decks = listDecks.data;
        this.isLoading = false
      }
    },
    watch: {
      filterProp: {
        handler: function(val){
          this.filter = val
          this.getListDecks(val, this.timeFrame, this.minGames)
        },
        deep: true
      },
      timeFrameProp: {
        handler: function(val){
          console.log(val);
          this.getListDecks(this.filter, val, this.minGames);
        },
        deep: true
      },
      minGamesProp: {
        handler: function(val){
          console.log(val);
          this.getListDecks(this.filter, this.timeFrame, val);
        },
        deep: true
      },
    }
  }
</script>