<template>
    <v-app style="position:relative;z-index:1;background-color:transparent">
        <Navbar />
        <v-card height="100%">
            <v-card-title class="ma-2">{{deck.archetypeName}}</v-card-title>
            <v-row>
                <v-col>
                    <v-data-table
                        :items="deck.cardsInDeck"
                        :headers="[
                            { text: 'Cost', value: 'card.cost', sortable: true },
                            { text: 'Card', value: 'cardImageName', sortable: false },
                            { text: 'Copies', value: 'count', sortable: false },
                        ]"
                        :disable-pagination="true"
                        :hide-default-footer="true"
                        sort-by="card.cost"
                    >
                        <template v-slot:item.cardImageName="{item}">
                                <v-avatar class="ma-2">
                                    <v-img :src="'./assets/'+item.cardCode+'-full.jpg'" />
                                </v-avatar>
                                {{ item.card.name }}
                        </template>
                    </v-data-table>
                </v-col>
                <v-col>
                    <v-row>
                        <v-card color="blue-grey darken-4">
                            <v-sparkline
                                :value="trendValues"
                                line-width="1"
                                padding="16"
                                class="trend-deck"
                                auto-draw
                                color="#78909C"
                            />
                        </v-card>
                    </v-row>
                    <v-row>
                        <v-text-field :value="deck.deckCode" label="Deck Code"/>
                    </v-row>
                </v-col>
            </v-row>
        </v-card>
    </v-app>
</template>
<script>
/*eslint no-console: ["error", { allow: ["log"] }] */
import Navbar from "./Navbar";
import * as get from "../../utils/get";

export default {
    components: {
        Navbar
    },
    data(){
        return {
            deck: {},
            isLoading: false,
            trendLabels: [],
            trendValues: [],
        }
    },
    async mounted(){
        await this.getDeck(get.getQueryParameter("code"));
        this.getDeckTrend(this.deck.deckId);
    },
    methods: {
      async getDeck(code) {
        this.isLoading = true;
        let deck = await get.getDeck(code);
        console.log(deck);
        this.deck = deck;
        this.isLoading = false
      },
      async getDeckTrend(id){
          let trend = await get.getDeckTrend(id);
          trend.map(day => {
            let date = day.timestamp.split("T")[0];
            this.trendLabels = [...this.trendLabels, date];
            let games = day.wins+day.losses;
            games = games ? games : 1; 
            let winrate = day.wins/(games);
            this.trendValues = [...this.trendValues, winrate];
          });
      }
    },
}
</script>
<style scoped>
.trend-deck > text {
    font-size: 10px !important;
}
</style>