export const getCard = async (value, field = "cardCode") => {
    let filter = `?filters=[{"field": "${field}", "value": "${value}"}]`;
    let url =  `https://lorstats.azurewebsites.net/api/Cards${filter}`;
    try {
        let response = await fetch(url, { 
            method: 'GET', 
            headers: new Headers({
                'Content-Type': 'application/json',
                'Authorization': `Bearer eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJodHRwOi8vc2NoZW1hcy54bWxzb2FwLm9yZy93cy8yMDA1LzA1L2lkZW50aXR5L2NsYWltcy9uYW1lIjoiNWRjM2U0YmM3YzIxM2U3MDhkMDZjZjRlIiwiZXhwIjoxNjA0OTUyMzA0LCJpc3MiOiJodHRwOi8vbG9jYWxob3N0OjUwMDYxLyIsImF1ZCI6Imh0dHA6Ly9sb2NhbGhvc3Q6NTAwNjEvIn0.gyU7KOuNP85emiYa6fIG_9AtaYPu6wR5V3jlAWY5V_U`
            })
        });
        let res = await response.json();
        return res.data[0];
    } catch (e) {
        return e.message;
    }
}

