export const getQueryParameter = (parameter = null) => {
    let uri = window.location.toString();
    let query = uri.split("?")[1];
    let res = JSON.parse('{"' + decodeURI(query).replace(/"/g, '\\"').replace(/&/g, '","').replace(/=/g,'":"') + '"}');
    if(parameter){
        return res[parameter];
    }
    else return res
}