<template>
    <div>
       <Navbar />
       <div id="playground" :style="playgroundStyle">
          <div 
            class="playground-half"
            >
              <div class="playground-half"/>
          </div>
          <div class="playground-half">
              <div class="playground-half"/>
          </div>
       </div>
    </div>
</template>

<script>
/*eslint no-console: ["error", { allow: ["log"] }] */
import Navbar from "../home/Navbar.vue";
import * as get from "../../utils/get.js";

export default {
  components: {
    Navbar
  },
  data() {
    return {
      currentRectangles: [],
      cardElements: [],
      currentScreen: { screenWidth: 0, screenHeight: 0 },
      currentPositionIndex: 0,
      replayInterval: null,
      renderInterval: null,
      games: [],
      playgroundStyle: `background-image:url(${"./assets/playground.jpg"})`
    }
  }, 
  methods: {
    lerp(v0, v1, t) {
      return (1 - t) * v0 + t * v1;
    },
    
    updateOrCreate(rectangle) {
      const foundElement = this.cardElements.find(cardElement => cardElement.cardID === rectangle.cardID);
      if(foundElement) {
        let cardElement = foundElement.element;
        const deltaTime = 0.2; //200 milisecond as T param for interpolation function

        //NOTE(DavoSK): -> interpolate positions for smoothing and recalculate position for current relative resolution 
        const interpolatedPosX  = this.lerp(foundElement.lastX, rectangle.topLeftX, deltaTime);
        const interpoaltedPosY  = this.lerp(foundElement.lastY, rectangle.topLeftY, deltaTime);

        const recalculatedPos 				= this.recalculateAxis(interpolatedPosX, interpoaltedPosY, foundElement.parent);
        cardElement.style.left 			  = `${recalculatedPos.X}px`;
        cardElement.style.top 				= `${foundElement.parent.clientHeight - recalculatedPos.Y}px`;

        foundElement.lastX = interpolatedPosX;
        foundElement.lastY = interpoaltedPosY;

        //NOTE(DavoSK) -> interpolate size and relaculate it too
        const interpolatedWidth       = this.lerp(foundElement.lastWidth, rectangle.width, deltaTime);
        const interpoaltedHeight      = this.lerp(foundElement.lastHeight, rectangle.height, deltaTime);

        const recalculatedSize 				= this.recalculateScaleAxis(interpolatedWidth, interpoaltedHeight, foundElement.parent);

        cardElement.style.width 			= `${recalculatedSize.X}px`;
        cardElement.style.height 			= `${recalculatedSize.Y}px`;
        cardElement.style.backgroundImage  = `url(${this.getCardAsset(rectangle)})`;

        //NOTE(DavoSK) -> buffs and spells are circle stuff :)
        cardElement.style.borderRadius = recalculatedSize.X === recalculatedSize.Y ? '50%' : 'none';
        
        foundElement.lastWidth  = interpolatedWidth;
        foundElement.lastHeight = interpoaltedHeight;

      } else {
        const playgroundParent = document.getElementById('playground');
        let newElement = document.createElement('div');
        newElement.style.position = 'absolute';
        newElement.style.backgroundSize = 'contain';
        newElement.className = "card";
        playgroundParent.appendChild(newElement);
        
        this.cardElements.push({
          cardID: rectangle.cardID, 
          lastX: rectangle.topLeftX, 
          lastY: rectangle.topLeftY, 
          lastWidth: rectangle.width,
          lastHeight: rectangle.height,
          element: newElement,
          parent: playgroundParent
        });
      }
    }, 

    getCardAsset(rectangle) {
      if(rectangle.width >= rectangle.height )
        return './assets/' + rectangle.cardCode + '.png';
      else
        return './assets/' + rectangle.cardCode + '.png';
    },

    recalculateAxis(_X, _Y, parent) {
      return { 
        X: parent.clientWidth * _X / this.currentScreen.screenWidth,
        Y: parent.clientHeight * _Y / this.currentScreen.screenHeight
      }
    },

    recalculateScaleAxis(_X, _Y, parent) {
       return { 
        X: (parent.clientWidth * _X / this.currentScreen.screenWidth),
        Y: (parent.clientHeight * _Y / this.currentScreen.screenHeight),
      }  
    },

    renderCards() {
      this.currentRectangles.map((rect)=> {
        this.updateOrCreate(rect);
      });
    },

    nextStep(nextPosition) {
      //NOTE(DavoSK): Dont include faces for now ( we dont have assets for them )
      this.currentRectangles = nextPosition.rectangles.map((rect)=> {
        if(rect.cardCode.indexOf('face') > -1){
          rect.cardCode = "nexus";
        }
        return rect;
      });

      //NOTE(DavoSK): Save screen resolucation for current position
      this.currentScreen = nextPosition.screen;

      //NOTE(DavoSK): Check if game didnt destroyed eny card, so we should do it too
      this.cardElements = this.cardElements.filter((cardElement)=> {
        const shouldRemove = this.currentRectangles.find( innerRect => innerRect.cardID === cardElement.cardID) == null;
        if(shouldRemove) {
          cardElement.element.remove();
        }
        return !shouldRemove;
      })
    }, 

    replayEnded() {
        //NOTE(DavoSK): clear all our elements and clear rendering array, 
        //also clear rendering interval
        this.cardElements.map((cardElement)=> {
          if(cardElement) {
            cardElement.element.remove();
          }
        })

        this.cardElements = [];
        if(this.renderInterval) {
          clearInterval(this.renderInterval);
        }
    }, 

    replayStart() {
      this.replayEnded();
      this.renderInterval = setInterval(this.renderCards, 50);
    },

    replayGame(game) {
      this.replayInterval = setInterval(()=> {
        if(this.currentPositionIndex > game.positions.length - 1)  {
          this.currentPositionIndex = 0;
          this.replayEnded();
        }

        if(this.currentPositionIndex === 0) {
          this.replayStart();
        }

        this.nextStep(game.positions[this.currentPositionIndex]);
        this.currentPositionIndex++
      }, 1000);
    }
  },
  beforeDestroy() {
    if(this.replayInterval) {
      clearInterval(this.replayInterval);
    }

    if(this.renderInterval) {
      clearInterval(this.renderInterval);
    }
  },
  async mounted() {
    let game  = await get.getGame(get.getQueryParameter("id"));
    console.log(game);
    this.replayGame(game);
  } 
};
</script>

<style>
.playground-half {
  width: 100%;
  height: 50%;
  position: relative;
  z-index: 2;
}
#playground {
  width: 90vw;
  height: 90vh;
  margin-left: auto;
  margin-right: auto;
  position: relative;
  overflow: hidden;
  z-index: 2;
}
.card {
    z-index: 3;
}
</style> 