<template>
    <v-app style="position:relative;z-index:1;background-color:transparent">
        <Navbar />
        <v-card height="100%">
            <v-img :src="'./assets/'+card.cardCode+'-full.jpg'" class="card-background"/>
            <v-card-title class="ma-2">{{card.name}}</v-card-title>
            <v-row>
                <v-col cols="3">
                    <v-img :src="'./assets/'+card.cardCode+'.png'" height="400px" width="260px" class="ma-2"/>
                </v-col>
                <v-col cols="4">
                    <v-card>
                        <v-list-item>
                            <v-list-item-content>Artist:</v-list-item-content>
                            <v-list-item-content>{{card.artistName}}</v-list-item-content>
                        </v-list-item>
                        <v-list-item>
                            <v-list-item-content>Asociated cards:</v-list-item-content>
                            <v-list-item-content>{{card.associatedCardRefs}}</v-list-item-content>
                        </v-list-item>
                        <v-list-item>
                            <v-list-item-content>Attack:</v-list-item-content>
                            <v-list-item-content>{{card.attack}}</v-list-item-content>
                        </v-list-item>
                        <v-list-item>
                            <v-list-item-content>Cost:</v-list-item-content>
                            <v-list-item-content>{{card.cost}}</v-list-item-content>
                        </v-list-item>
                        <v-list-item>
                            <v-list-item-content>Description:</v-list-item-content>
                            <v-list-item-content>{{card.descriptionRaw}}</v-list-item-content>
                        </v-list-item>
                        <v-list-item>
                            <v-list-item-content>Flavor text:</v-list-item-content>
                            <v-list-item-content>{{card.flavorText}}</v-list-item-content>
                        </v-list-item>
                        <v-list-item>
                            <v-list-item-content>Health:</v-list-item-content>
                            <v-list-item-content>{{card.health}}</v-list-item-content>
                        </v-list-item>
                        <v-list-item>
                            <v-list-item-content>Rarity:</v-list-item-content>
                            <v-list-item-content>{{card.rarity}}</v-list-item-content>
                        </v-list-item>
                        <v-list-item>
                            <v-list-item-content>Region:</v-list-item-content>
                            <v-list-item-content>{{card.region}}</v-list-item-content>
                        </v-list-item>
                        <v-list-item>
                            <v-list-item-content>Spell Speed:</v-list-item-content>
                            <v-list-item-content>{{card.spellSpeed}}</v-list-item-content>
                        </v-list-item>
                        <v-list-item>
                            <v-list-item-content>Type:</v-list-item-content>
                            <v-list-item-content>{{card.type}}</v-list-item-content>
                        </v-list-item>
                    </v-card>
                </v-col>
                <v-col>
                    <v-card color="blue-grey darken-4">
                        <v-sparkline
                            :value="trendValues"
                            line-width="1"
                            padding="16"
                            class="trend-deck"
                            auto-draw
                            color="#78909C"
                        />
                    </v-card>
                    <v-card-subtitle>
                        Recently used in decks
                    </v-card-subtitle>
                    <v-data-table
                        :headers="headers"
                        :items="decks"
                        :sort-by="['winrate', 'games']"
                        :sort-desc="[true, true]"
                        multi-sort
                        class="elevation-1"
                        style="height:100%"
                        :loading="isLoadingDecks"
                    >
                        <template v-slot:item.winrate="{ item }">
                            {{item.winrate}} %
                        </template>
                        <template v-slot:item.detail="{ item }">
                            <v-btn :to="`/deck?code=${item.deckCode}`">
                                Show Details
                            </v-btn>
                        </template>
                    </v-data-table>
                </v-col>
            </v-row>
        </v-card>
    </v-app>
</template>
<script>
/*eslint no-console: ["error", { allow: ["log"] }] */
import Navbar from "../home/Navbar";
import * as get from "../../utils/get";

export default {
    components: {
        Navbar
    },
    data(){
        return {
            card: {},
            isLoading: false,
            trendValues: [],
            trendLabels: [],
            isLoadingDecks: false,
            headers: [
                { text: 'Archetype', value: 'archetype', sortable: false },
                { text: 'Winrate', value: 'winrate' },
                { text: 'Games', value: 'games' },
                { text: '', value: 'detail', sortable: false }
            ],
            decks: [],
        }
    },
    async mounted(){
        await this.getCard(get.getQueryParameter("code"));
        this.getCardTrend(this.card.cardId);
        this.getListDecks();
    },
    methods: {
      async getCard(code) {
        this.isLoading = true;
        let card = await get.getCard(code);
        console.log(card);
        this.card = card;
        this.isLoading = false
      },
      async getCardTrend(id){
          let trend = await get.getCardTrend(id);
          trend.map(day => {
            let date = day.timestamp.split("T")[0];
            this.trendLabels = [...this.trendLabels, date];
            let games = day.wins+day.losses;
            games = games ? games : 1; 
            let winrate = day.wins/(games);
            this.trendValues = [...this.trendValues, winrate];
          });
      },
      async getListDecks () {
        this.isLoadingDecks = true;
        let listDecks = await get.getDecks(`[{"field": "cardsInDeck.cardCode", "value":"${this.card.cardCode}"}]`, 30);
        listDecks.data.forEach((deck) => {
          deck.archetype = deck.factions.join(" / ")
          deck.games = deck.wins+deck.losses;
          deck.games ? deck.winrate = Math.round(deck.wins/(deck.games)*10000)/100 : deck.winrate = 0;
        });
        this.decks = listDecks.data;
        this.isLoadingDecks = false
      }
    },
}
</script>
<style scoped>
.card-background {
    width: 100%;
    height: auto;
    -webkit-mask-image: -webkit-gradient(linear, left top, left bottom, from(rgba(0,0,0,0.2)), to(rgba(0,0,0,0)));
    mask-image: linear-gradient(to bottom,
    rgba(0,0,0,0.2),
    rgba(0,0,0,0));
    position: absolute;
}
</style>